<template>
  <div class="docs_container">
    <pr-row>
      <pr-col span="4">
        <!-- <div>LOGO</div> -->
        <div class="docs_side_bar">
          <pr-scroller
            scroll-y="calc(100vh - 51px)"
            :bar-style="barStyle"
            :thumb-style="thumbStyle"
          >
            <div class="docs_side_bar_wrapper">
              <ul class="docs_list">
                <li v-for="doc in docs" :key="doc.name">
                  <h3>{{ doc.title }}</h3>
                  <router-link
                    tag="span"
                    v-for="child in doc.children"
                    :to="`/doc/${child.path}`"
                    :key="child.name"
                  >
                    {{ child.title || child.name }}
                  </router-link>
                </li>
              </ul>
            </div>
          </pr-scroller>
        </div>
      </pr-col>
      <pr-col span="20">
        <div class="docs_content">
          <router-view />
        </div>
      </pr-col>
    </pr-row>
  </div>
</template>

<script>
import "@/assets/style/docs.less";
import docs from "../gnozValidatorDoc";
export default {
  name: "docs",
  data() {
    return {
      docs,
      barStyle: {
        x: {
          background: "transparent",
        },
        y: {
          background: "transparent",
        },
      },
      thumbStyle: {
        background: "#c1c1c1",
      },
    };
  },
};
</script>



